.wpglobus-switcher-panel__body {
	position: absolute;
	background-color: #efefef;
	right: 10px;
	border: 1px solid #dfdfdf;
	width: 250px;
	padding-bottom: 5px;
}

.wpglobus-switcher-panel__body.hidden {
	/* display:none; */
}

.wpglobus-switcher-panel__body.is-opened {
	padding-top: 0;
	/*padding-bottom: 0;*/
}

.wpglobus-switcher-panel__body .language-flag {
	margin-right: 7px;
	width: 20px;
	vertical-align: middle;
	height: auto
}


